.App {
  display: flex;
  align-items: center;
  text-align: center;
  flex-direction: column;
  min-height: 100vh;
}

.frame {
  width: 780px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  flex-direction: column;
}

h1 {
  display: inline-block;
  font-size: 20px;
}

.ItemName {
  display: block;
  width: 560px;
  text-align: left;
  font-size: 35px;
  font-weight: 400;
  color: #333333;
}

.Title {
  width: 560px;
  height: 560px;
  display: block;
  overflow: hidden;
  border: 10px solid #cdcdcd;
}

.Option {
  display: block;
  width: 560px;
  text-align: left;
}

.opt {
  display: block;
  width: 560px;
}

.option_nm {
  display: inline-block;
  color: #242424;
  width: 130px;
  font-size: 34px;
  font-weight: bold;
}

.option_val {
  display: inline-block;
  margin-left: 20px;
  color: #242424;
  font-size: 25px;
  font-weight: light;
}

.item {
  margin-top: 30px;
  margin-bottom: 30px;
}

.Images {
  width: 740px;
  min-height: 500px;
}


.topBar {
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  position: sticky; 
  top: 0px;
  background: #eeeeee;
  z-index: 20;
  text-align: center;
}

.edit {
  width:80px;
  height:30px;
  background:#74a7ff;
  border:none;
  margin:3px;
  font-size: 15px;
  font-weight: bold;
  color: white;
  cursor: default;
  -ms-user-select: none; 
  -moz-user-select: -moz-none; 
  -webkit-user-select: none; 
  -khtml-user-select: none; 
  user-select:none;
}

.remove {
  width:100px;
  height:30px;
  background:#48c737;
  border:none;
  margin:3px;
  font-size: 15px;
  font-weight: bold;
  color: white;
  cursor: default;
  -ms-user-select: none; 
  -moz-user-select: -moz-none; 
  -webkit-user-select: none; 
  -khtml-user-select: none; 
  user-select:none;
}

.download {
  width:80px;
  height:30px;
  background:#ff8874;
  border:none;
  margin:3px;
  font-size: 15px;
  font-weight: bold;
  color: white;
  cursor: default;
  -ms-user-select: none; 
  -moz-user-select: -moz-none; 
  -webkit-user-select: none; 
  -khtml-user-select: none; 
  user-select:none;
}